import React, { useState } from "react"
import { graphql } from "gatsby"
import Layout from "./src/components/layout"

const MarkDownTemplate = ({ data }) => {
  const [style, setStyle] = useState({ clickerRight: '0', contentsRight: '-400px' })
  const post = data.markdownRemark
  const onClickTOC = () => {
    setStyle({ ...style, clickerRight: '-300px', contentsRight: '0' })
  }
  const onClickClose = () => {
    setStyle({ ...style, clickerRight: '0', contentsRight: '-400px' })
  }
  return (
    <Layout>
      <div className='markdownTemplate' style={{ position: 'relative', marginBottom: '200px', lineHeight: '1.7' }}>
        <h4 style={{ paddingBottom: '5px', borderBottom: '1px solid gray' }}>
          <span style={{ color: 'gray' }}>{post.frontmatter.title}</span>
          <span style={{ float: 'right', color: 'red' }}>{post.frontmatter.date}</span>
        </h4>
        <div dangerouslySetInnerHTML={{ __html: post.html }} />
        <div className='contentsClicker' style={{ right: style.clickerRight }} aria-hidden='true' onClick={onClickTOC} onKeyDown={onClickTOC}>목차</div>
        <div className='contents' style={{ right: style.contentsRight }}>
          <div aria-hidden='true' onClick={onClickClose} onKeyDown={onClickClose}>x</div>
          <div dangerouslySetInnerHTML={{ __html: post.tableOfContents }}></div>
        </div>
      </div>
    </Layout>
  )
}

export default MarkDownTemplate

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        date(formatString: "DD MMMM, YYYY")
      }
      tableOfContents
    }
  }
`