import React, { useState, useEffect, useCallback } from "react"
import { Link } from "gatsby"

const Header = () => {
  const [isMain, setIsMain] = useState(true)
  const [windowWidth, setWindowWidth] = useState(700)
  const [hamburgerClicked, setHamburgerClicked] = useState(false)

  const hadleScroll = useCallback(() => {
    const currentScrollPosition = window.pageYOffset
    const currenBrowserHeight = window.innerHeight
    if (currentScrollPosition >= currenBrowserHeight) {
      setIsMain(false)
    } else {
      setIsMain(true)
    }
  }, [])

  const handleWidth = useCallback(() => {
    const newWidth = window.innerWidth
    setWindowWidth(newWidth)
    if (newWidth > 500) {
      setHamburgerClicked(false)
    }
  }, [])

  useEffect(() => {
    if (window.location.pathname === '/') {
      window.addEventListener('scroll', hadleScroll)
      setIsMain(true)
    } else {
      setIsMain(false)
    }
    return () => {
      window.removeEventListener('scroll', hadleScroll)
    }
  }, [hadleScroll])

  useEffect(() => {
    window.addEventListener('resize', handleWidth)
    return () => {
      window.removeEventListener('resize', handleWidth)
    }
  }, [handleWidth])

  const onClickHamburger = () => {
    if (!hamburgerClicked) {
      setHamburgerClicked(true)
    } else {
      setHamburgerClicked(false)
    }
  }

  const headerStyle = isMain ? { paddingTop: '2.5vh', paddingBottom: '2.5vh', position: 'absolute', top: 'calc(100vh - 71px)', left: '0', transition: 'top .2s', color: 'white', display: 'flex', justifyContent: 'space-between' } : { transition: 'color .2s', backgroundColor: 'rgba(255, 255, 255, 0.5)', borderBottom: '#e9e9e9 1px solid', zIndex: '3', backdropFilter: 'blur(5px)', paddingTop: '2.5vh', paddingBottom: '2.5vh', position: 'fixed', top: 0, left: 0, display: 'flex', justifyContent: 'space-between' }
  const imgStyle = isMain ? { top: '-2px', filter: 'opacity(.6) drop-shadow(0 0 0 white)', position: 'absolute', width: '26px', height: '26px', color: 'white', backgroundImage: 'url(https://user-images.githubusercontent.com/71132893/103412811-4ba46f80-4bba-11eb-94b9-454fc8d16d8d.png)', backgroundSize: 'contain', backgroundRepeat: 'no-repeat' } : { top: '-2px', position: 'absolute', width: '26px', height: '26px', backgroundImage: 'url(https://user-images.githubusercontent.com/71132893/103412811-4ba46f80-4bba-11eb-94b9-454fc8d16d8d.png)', backgroundSize: 'contain', backgroundRepeat: 'no-repeat' }
  const titleStyle = isMain ? { lineHeight: '1.2', position: 'absolute', width: '90px', left: '30px', color: 'white', fontSize: '27px', top: '-5px', } : { lineHeight: '1.2', fontSize: '27px', position: 'absolute', color: 'black', width: '90px', left: '30px', top: '-5px' }
  const linkStyle = isMain ? { textDecoration: 'none', color: 'white', transition: 'color .1s linear' } : { textDecoration: 'none', color: 'black', transition: 'color .1s linear' }

  return (
    <header style={headerStyle}>
      <div>
        <Link to="/" style={{ position: 'relative', textDecoration: `none`, }}>
          <div style={imgStyle}></div>
          <div className='header-title' style={titleStyle}>
            <span>Ho'space</span>
          </div>
        </Link>
      </div>
      {windowWidth > 500 ?
        <ul style={{ width: '261px', margin: '0', padding: '0', listStyle: `none`, display: 'flex', justifyContent: 'space-between' }}>
          <li>
            <Link className='header-link' style={linkStyle} to="/about/">About</Link>
          </li>
          <li>
            <Link className='header-link' style={linkStyle} to="/projects/">Projects</Link>
          </li>
          <li>
            <Link className='header-link' style={linkStyle} to="/blog/">Blog</Link>
          </li>
          <li>
            <Link className='header-link' style={linkStyle} to="/contact/">Contact</Link>
          </li>
        </ul> :
        <>
          <button className='hamburger' onClick={onClickHamburger}>
            <span style={{ backgroundColor: isMain ? 'white' : 'black', transform: hamburgerClicked && 'rotate(-45deg) translate(-7px, 6px)' }}></span>
            <span style={{ backgroundColor: isMain ? 'white' : 'black', opacity: hamburgerClicked && '0' }}></span>
            <span style={{ backgroundColor: isMain ? 'white' : 'black', transform: hamburgerClicked && 'rotate(45deg) translate(-7px, -6px)' }}></span>
          </button>
          <ul style={{ position: 'absolute', top: '72px', left:hamburgerClicked?'0':'-100vw',listStyle: 'none', padding: 0, margin: 0, width: '100vw', textAlign: 'center', backgroundColor: 'rgba(255,255,255,0.5)', backdropFilter: 'blur(5px)',transition:'all .4s' }}>
            <li>
              <Link className='header-link' style={{ fontSize: '50px', fontWeight: '300', textDecoration: 'none', color: 'black', transition: 'all .3s' }} to="/about/">About</Link>
            </li>
            <li>
              <Link className='header-link' style={{ fontSize: '50px', fontWeight: '300', textDecoration: 'none', color: 'black', transition: 'all .3s' }} to="/projects/">Projects</Link>
            </li>
            <li>
              <Link className='header-link' style={{ fontSize: '50px', fontWeight: '300', textDecoration: 'none', color: 'black', transition: 'all .3s' }} to="/blog/">Blog</Link>
            </li>
            <li>
              <Link className='header-link' style={{ fontSize: '50px', fontWeight: '300', textDecoration: 'none', color: 'black', transition: 'all .3s' }} to="/contact/">Contact</Link>
            </li>
          </ul>
        </>}
    </header >
  )
}

export default Header
